<template>
	<div class="selected-opt--wrap">
		<h3 class="sub-header">Вы указали:</h3>
		<table class="selected-opt">
			<tr class="selected-opt--tr">
				<th class="selected-opt--td selected-opt--th">Тип транспортного средства:</th>
				<th class="selected-opt--td selected-opt--th">{{transExtraName}}:</th>
				<th class="selected-opt--td selected-opt--th">Место регистрации ТС:</th>
				<th class="selected-opt--td selected-opt--th">Владелец ТС:</th>
				<th class="selected-opt--td selected-opt--th">Используеться ли ТС как такси?:</th>
				<th class="selected-opt--td selected-opt--th">Период страхования:</th>
				<th class="selected-opt--td selected-opt--th">Период без ДТП:</th>
				<th class="selected-opt--td selected-opt--th">Права на льготы:</th>
				<th class="selected-opt--td selected-opt--th">Расширить лимиты ОСАГО на:</th>
			</tr>
			<tr class="selected-opt--tr">
				<td class="selected-opt--td">{{transportData.trans.type}}</td>
				<td class="selected-opt--td">{{transportData.trans.extra}}</td>
				<td class="selected-opt--td">{{transportData.place.foreign || transportData.place.city}}</td>
				<td class="selected-opt--td">{{transportData.user}}</td>
				<td class="selected-opt--td">{{transportData.taxi ? "Да" : "Нет"}}</td>
				<td class="selected-opt--td">{{transportData.period.term}}</td>
				<td class="selected-opt--td">{{transportData.dtp ? transportData.dtp : "ДТП не было"}}</td>
				<td class="selected-opt--td">{{transportData.privilege ? transportData.privilege : "Нету"}}</td>
				<td class="selected-opt--td">{{transportData.osagoLimits}}</td>
			</tr>
		</table>
		<router-link class="form--link" to="/">Изменить данные</router-link>
	</div>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";
	
	export default {
		data() {
			return {

			}
		},
		computed: {
			...mapGetters("transport",{
				"transExtraName": "transExtraName"
			}),
			...mapGetters([
				"transportData"
			])
		}
	}
</script>

<style lang="less">
.selected-opt {
	display: block;

	&--wrap {
		font-size: 19px;
		margin-bottom: 40px;
		@media only screen and (max-width: 1195px) {
 			font: inherit;
 		}
	}

	&--tr {
		display: inline-block;
		margin-right: 5px;
		margin-top: 10px;
		margin-bottom: 15px;
	}

	&--th {
		font-weight: bold;
	}

	&--td {
		display: block;
		margin-bottom: 7px;
	}
}
</style>




